import React, { useRef, useEffect, useState } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ScrollView, SafeAreaView, Image, Dimensions, Platform, Animated } from 'react-native';
import { useNavigation, useRoute, useFocusEffect } from '@react-navigation/native';

const { width: SCREEN_WIDTH } = Dimensions.get('window');
const BUTTON_WIDTH = Math.min(320, SCREEN_WIDTH * 0.8);

// Shipping Button Component
const ShippingButton = ({ type, price, onPress, isSelected }) => (
  <TouchableOpacity 
    style={[
      styles.shippingButton,
      isSelected && styles.selectedShippingButton
    ]}
    onPress={onPress}
  >
    <View style={styles.shippingContent}>
      <Text style={styles.shippingButtonText}>{type}</Text>
      <View style={[styles.pricingValueContainer, isSelected && styles.selectedPricingValueContainer]}>
        <Text style={styles.priceText}>
          {price === 'FREE' ? 'FREE' : `A$ ${price}`}
        </Text>
      </View>
    </View>
  </TouchableOpacity>
);

// Price Display Component
const PriceDisplay = ({ price, hasProtection, withShipping, titleFade, priceFade }) => (
  <View style={styles.pricingCard}>
    <Animated.Text style={[styles.pricingHeader, { opacity: titleFade }]}>
      {withShipping ? 'Price with Express Shipping' : 
       (hasProtection ? 'Hose Price with Protection' : 'Hose Only Price')}
    </Animated.Text>
    <View style={styles.pricingValueContainer}>
      <Animated.Text style={[styles.pricingValue, { opacity: priceFade }]}>
        A$ {price.toFixed(2)}
      </Animated.Text>
    </View>
  </View>
);

const ShippingOptionsScreen = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const [selectedService, setSelectedService] = useState(null);
  const [basePrice, setBasePrice] = useState(0);
  const [shippingPrice, setShippingPrice] = useState(0);
  const priceFade = useRef(new Animated.Value(1)).current;
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const titleFade = useRef(new Animated.Value(1)).current;

  const { 
    selectedHose,
    selectedHosePrice, 
    selectedFitting, 
    end1Shape, 
    end1Size,
    end1Price, 
    selectedEnd2Fitting, 
    end2Shape, 
    end2Size,
    end2Price,
    selectedAngle, 
    quantity, 
    cutLengths, 
    selectedProtection,
    basePrice: receivedBasePrice,
    totalPrice: receivedTotalPrice,
    protectionCost,
    hoseProtectionPrice,
    selectedPressure,
    pressureTestPrice,
    cutLengthPrice,  // Added this
    totalBeforeShipping  // Added this
  } = route.params || {};

  useEffect(() => {
    if (totalBeforeShipping) {
      setBasePrice(totalBeforeShipping);
      setShippingPrice(totalBeforeShipping);
    } else if (hoseProtectionPrice) {
      setBasePrice(hoseProtectionPrice);
      setShippingPrice(hoseProtectionPrice);
    } else if (cutLengthPrice) {
      setBasePrice(cutLengthPrice);
      setShippingPrice(cutLengthPrice);
    }
  }, [totalBeforeShipping, hoseProtectionPrice, cutLengthPrice]);

  useEffect(() => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: true,
    }).start();
  }, []);

  useFocusEffect(
    React.useCallback(() => {
      //setSelectedService(null);
      fadeAnim.setValue(0);
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: true,
      }).start();
    }, [fadeAnim])
  );

  const handleServiceSelection = (service) => {
    setSelectedService(service);
    
    if (service === 'STANDARD') {
      // Standard shipping navigation
      Animated.timing(fadeAnim, {
        toValue: 0,
        duration: 800,
        useNativeDriver: true,
      }).start(() => {
        navigateToNext(service, basePrice);
      });
    } else {
      // Express shipping with animation
      Animated.sequence([
        Animated.parallel([
          Animated.timing(titleFade, {
            toValue: 0,
            duration: 500,
            useNativeDriver: true,
          }),
          Animated.timing(priceFade, {
            toValue: 0,
            duration: 500,
            useNativeDriver: true,
          })
        ]),
        Animated.timing(new Animated.Value(0), {
          toValue: 1,
          duration: 100,
          useNativeDriver: true,
        }),
      ]).start(() => {
        const newPrice = basePrice + 10;
        setShippingPrice(newPrice);
        
        Animated.parallel([
          Animated.timing(titleFade, {
            toValue: 1,
            duration: 500,
            useNativeDriver: true,
          }),
          Animated.timing(priceFade, {
            toValue: 1,
            duration: 500,
            useNativeDriver: true,
          })
        ]).start(() => {
          setTimeout(() => {
            Animated.timing(fadeAnim, {
              toValue: 0,
              duration: 800,
              useNativeDriver: true,
            }).start(() => {
              navigateToNext(service, newPrice);
            });
          }, 1000);
        });
      });
    }
  };

  const navigateToNext = (service, finalPrice) => {
    navigation.navigate('OrderConfirmation1', {
      selectedService: service,
      cutLengths,
      selectedHose,
      selectedHosePrice,
      selectedFitting,
      end1Shape,
      end1Size,
      end1Price,
      selectedEnd2Fitting,
      end2Shape,
      end2Size,
      end2Price,
      selectedAngle,
      quantity,
      selectedProtection,
      hoseProtectionPrice,
      selectedPressure,
      pressureTestPrice,
      basePrice: receivedBasePrice,
      totalBeforeShipping: totalBeforeShipping || hoseProtectionPrice || cutLengthPrice,
      cutLengthPrice: cutLengthPrice,
      shippingCost: service === 'EXPRESS' ? 10 : 0,
      totalPriceWithShipping: finalPrice,
      protectionCost,
    });
  };

  return (
    <SafeAreaView style={styles.container}>
      {/* Add Floating Back Button here */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Text style={styles.backButtonText}></Text>
      </TouchableOpacity>
      <View style={styles.logoContainer}>
        <Image
          source={require('../assets/fluidpower_logo.gif')}
          style={styles.logo}
          resizeMode="contain"
        />
      </View>
      <Animated.View style={[styles.animatedContainer, { opacity: fadeAnim }]}>
        <View style={styles.titleContainer}>
          <View style={styles.titleWrapper}>
            <Text style={styles.titleText}>Select Shipping OPTION</Text>
          </View>
        </View>

        <ScrollView style={styles.scrollView} contentContainerStyle={styles.scrollViewContent}>
          <ShippingButton
            type="STANDARD"
            price="FREE"
            onPress={() => handleServiceSelection('STANDARD')}
            isSelected={selectedService === 'STANDARD'}
          />
          <ShippingButton
            type="EXPRESS"
            price="10.00"
            onPress={() => handleServiceSelection('EXPRESS')}
            isSelected={selectedService === 'EXPRESS'}
          />

          <View style={styles.pricingSection}>
            <PriceDisplay 
              price={selectedService === 'EXPRESS' ? shippingPrice : basePrice}
              hasProtection={selectedProtection && selectedProtection !== 'NOT REQUIRED'}
              withShipping={selectedService === 'EXPRESS'}
              titleFade={titleFade}
              priceFade={priceFade}
            />
          </View>
        </ScrollView>
      </Animated.View>
    </SafeAreaView>
  );
};

