import React, { useState, useRef, useEffect } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ScrollView, SafeAreaView, Image, Dimensions, Platform, Animated, Modal, TouchableWithoutFeedback } from 'react-native';
import { useNavigation, useFocusEffect } from '@react-navigation/native';

const { width: SCREEN_WIDTH } = Dimensions.get('window');
const MAX_COLUMN_WIDTH = 400;
const BUTTON_WIDTH = Math.min(320, SCREEN_WIDTH * 0.8);

const FittingTypeButton = ({ type, description, onPress, isSelected, onHelpPress }) => (
  <TouchableOpacity 
    style={[styles.fittingButton, isSelected && styles.selectedFittingButton]} 
    onPress={onPress}
  >
    <View style={styles.fittingContent}>
      <Text style={styles.fittingButtonText}>{type}</Text>
      <Text style={styles.fittingDescription}>{description}</Text>
    </View>
    <TouchableOpacity style={styles.helpIcon} onPress={onHelpPress}>
      <Text style={styles.helpIconText}>?</Text>
    </TouchableOpacity>
  </TouchableOpacity>
);

const End2FittingScreen = ({ route }) => {
  const navigation = useNavigation();
  const [selectedEnd2Fitting, setSelectedEnd2Fitting] = useState(null);
  const { selectedHose, selectedHosePrice, selectedFitting, end1Shape, end1Size, end1Price } = route.params || {};
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const [showHelpOverlay, setShowHelpOverlay] = useState(false);
  const [currentHelpImage, setCurrentHelpImage] = useState(null);
  const helpFadeAnim = useRef(new Animated.Value(0)).current;

  const fittingTypes = [
    { type: 'BSP 60', description: 'British Standard Pipe', helpImage: require('../assets/bsp_help.png') },
    { type: 'JIC 37', description: 'Joint Industrial Council', helpImage: require('../assets/jic_help.png') },
    { type: 'METRIC LIGHT 24', description: 'Cone O-Ring', helpImage: require('../assets/metric_help.png') },
    { type: 'O.R.F.S', description: 'O-Ring Flat Seal', helpImage: require('../assets/orfs_help.png') },
  ];

  useEffect(() => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 500,
      useNativeDriver: true,
    }).start();
  }, []);

  useFocusEffect(
    React.useCallback(() => {
      //setSelectedEnd2Fitting(null);
      fadeAnim.setValue(0);
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 500,
        useNativeDriver: true,
      }).start();

      return () => {
        // Optional cleanup if needed
      };
    }, [fadeAnim])
  );

  const handleFittingSelection = (type) => {
    setSelectedEnd2Fitting(type);
    
    Animated.timing(fadeAnim, {
      toValue: 0,
      duration: 800,
      useNativeDriver: true,
    }).start(() => {
      switch (type) {
        case 'BSP 60':
          navigation.navigate('BSPFittingScreen2', { selectedHose, selectedHosePrice, selectedFitting, end1Shape, end1Size, end1Price, selectedEnd2Fitting: type });
          break;
        case 'JIC 37':
          navigation.navigate('JICFittingScreen2', { selectedHose, selectedHosePrice, selectedFitting, end1Shape, end1Size, end1Price, selectedEnd2Fitting: type });
          break;
        case 'METRIC LIGHT 24':
          navigation.navigate('MetricFittingScreen2', { selectedHose, selectedHosePrice, selectedFitting, end1Shape, end1Size, end1Price, selectedEnd2Fitting: type });
          break;
        case 'O.R.F.S':
          navigation.navigate('OrfsScreen2', { selectedHose, selectedHosePrice, selectedFitting, end1Shape, end1Size, end1Price, selectedEnd2Fitting: type });
          break;
        default:
          console.log('Selected Hose:', selectedHose);
          console.log('Selected Fitting:', type);
      }
    });
  };

  const handleHelpPress = (helpImage) => {
    setCurrentHelpImage(helpImage);
    setShowHelpOverlay(true);
    helpFadeAnim.setValue(0);
    Animated.timing(helpFadeAnim, {
      toValue: 1,
      duration: 500,
      useNativeDriver: true,
    }).start();
  };

  const closeHelpOverlay = () => {
    Animated.timing(helpFadeAnim, {
      toValue: 0,
      duration: 300,
      useNativeDriver: true,
    }).start(() => {
      setShowHelpOverlay(false);
    });
  };

  return (
    <SafeAreaView style={styles.container}>
    {/* Add Floating Back Button here */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
        >
        <Text style={styles.backButtonText}></Text>
        </TouchableOpacity>
      <View style={styles.logoContainer}>
        <Image
          source={require('../assets/fluidpower_logo.gif')}
          style={styles.logo}
          resizeMode="contain"
        />
      </View>
      
      <Animated.View style={[styles.contentContainer, { opacity: fadeAnim }]}>
        <View style={styles.titleContainer}>
          <View style={styles.titleWrapper}>
            <Text style={styles.titleText}>Select END </Text>
            <View style={styles.circleWrapper}>
              <Text style={styles.circleText}>2</Text>
            </View>
            <Text style={styles.titleText}> Thread</Text>
          </View>
        </View>
        
        <Image
          source={require('../assets/end2_thread_diagram.png')}
          style={styles.diagram}
          resizeMode="contain"
        />
        
        <ScrollView style={styles.scrollView} contentContainerStyle={styles.scrollViewContent}>
          {fittingTypes.map((fitting, index) => (
            <FittingTypeButton
              key={index}
              type={fitting.type}
              description={fitting.description}
              onPress={() => handleFittingSelection(fitting.type)}
              isSelected={selectedEnd2Fitting === fitting.type}
              onHelpPress={() => handleHelpPress(fitting.helpImage)}
            />
          ))}
        </ScrollView>
      </Animated.View>

      <Modal
        transparent={true}
        visible={showHelpOverlay}
        onRequestClose={closeHelpOverlay}
      >
        <TouchableWithoutFeedback onPress={closeHelpOverlay}>
          <View style={styles.modalOverlay}>
            <View style={styles.overlayBackground} />
            <Animated.View style={[styles.helpImageContainer, { opacity: helpFadeAnim }]}>
              <TouchableOpacity 
                style={styles.closeButton}
                onPress={closeHelpOverlay}
              >
                <Text style={styles.closeButtonText}></Text>
              </TouchableOpacity>
              <Image
                source={currentHelpImage}
                style={styles.helpImage}
                resizeMode="contain"
              />
            </Animated.View>
          </View>
        </TouchableWithoutFeedback>
      </Modal>
    </SafeAreaView>
  );
};

