// utils/emailService.js

const BREVO_API_KEY = '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';
const BREVO_API_URL = 'https://api.sendinblue.com/v3/smtp/email';

export const sendOrderEmail = async (orderDetails) => {
  // Calculate price breakdown helper function
  const calculatePriceBreakdown = (details) => {
    if (details.isOrderFittingMode) {
      const subtotal = details.cutLengthPrice || 0;
      const shipping = 10; // Fixed $10 shipping for Fitting Mode
      const subtotalWithShipping = subtotal + shipping;
      const gst = subtotalWithShipping * 0.1; // 10% GST
      const total = subtotalWithShipping + gst;
      
      return {
        subtotal,
        shipping,
        gst,
        total
      };
    } else {
      const subtotal = details.totalBeforeShipping || 0;
      const shipping = details.shippingCost || 0;
      const gst = (subtotal + shipping) * 0.1;
      const total = subtotal + shipping + gst;
      
      return {
        subtotal,
        shipping,
        gst,
        total
      };
  }
}

  // Convert PDF buffer to base64 if present
  let attachments = [];
  if (orderDetails.pdfAttachment) {
    const uint8Array = new Uint8Array(orderDetails.pdfAttachment);
    const binaryString = uint8Array.reduce((acc, byte) => acc + String.fromCharCode(byte), '');
    const pdfBase64 = btoa(binaryString);
    
    attachments.push({
      name: `Order-${orderDetails.orderNumber || 'unknown'}.pdf`,
      content: pdfBase64
    });
  }

  // Calculate price breakdown
  const priceBreakdown = calculatePriceBreakdown(orderDetails);

  // Format price display helper
  const formatPrice = (amount) => `A$${amount.toFixed(2)}`;
  
  // Price breakdown HTML template
  const priceBreakdownHTML = `
    <h3>Price Breakdown:</h3>
    <table style="width: 100%; max-width: 400px; margin: 10px 0;">
      <tr>
        <td><strong>Subtotal:</strong></td>
        <td style="text-align: right">${formatPrice(priceBreakdown.subtotal)}</td>
      </tr>
      <tr>
        <td><strong>Shipping:</strong></td>
        <td style="text-align: right">${formatPrice(priceBreakdown.shipping)}</td>
      </tr>
      <tr>
        <td><strong>GST (10%):</strong></td>
        <td style="text-align: right">${formatPrice(priceBreakdown.gst)}</td>
      </tr>
      <tr style="border-top: 1px solid #ccc;">
        <td><strong>Total Amount:</strong></td>
        <td style="text-align: right"><strong>${formatPrice(priceBreakdown.total)}</strong></td>
      </tr>
    </table>
  `;

  // Business email content
  const businessEmailPayload = {
    sender: {
      name: "FluidPower Group Order System",
      email: "info@agcomponents.com.au"
    },
    to: [{
      email: "info@fluidpowergroup.com.au",
      name: "FluidPower Group"
    }],
    subject: `New Order Received - Order #${orderDetails.orderNumber || 'Unknown'}`,
    attachment: attachments,
    htmlContent: `
      <h2>New Order Received</h2>
      <p>A new order has been placed with the following details:</p>
      <p><strong>Order Number:</strong> ${orderDetails.orderNumber || 'Unknown'}</p>
      ${priceBreakdownHTML}
      <p><strong>Customer Details:</strong></p>
      <ul>
        <li><strong>Name:</strong> ${orderDetails.userDetails?.name || 'N/A'}</li>
        <li><strong>Email:</strong> ${orderDetails.userDetails?.email || 'N/A'}</li>
        <li><strong>Contact:</strong> ${orderDetails.userDetails?.contactNumber || 'N/A'}</li>
      </ul>
      <p><strong>Shipping Address:</strong></p>
      <p>
        ${orderDetails.userDetails?.address || 'N/A'}<br>
        ${orderDetails.userDetails?.suburb || ''} ${orderDetails.userDetails?.state || ''} ${orderDetails.userDetails?.postcode || ''}
      </p>
    `
  };

  // Send business email
  try {
    const businessResponse = await fetch(BREVO_API_URL, {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'api-key': BREVO_API_KEY,
      },
      body: JSON.stringify(businessEmailPayload)
    });

    if (!businessResponse.ok) {
      const data = await businessResponse.json();
      throw new Error(`Business Email Error: ${businessResponse.status} - ${JSON.stringify(data)}`);
    }

    // If customer email exists, send customer email
    if (orderDetails.userDetails?.email) {
      const customerEmailPayload = {
        sender: {
          name: "FluidPower Group",
          email: "info@agcomponents.com.au"
        },
        to: [{
          email: orderDetails.userDetails.email,
          name: orderDetails.userDetails.name || 'Valued Customer'
        }],
        subject: `Your Order Confirmation - Order #${orderDetails.orderNumber || 'Unknown'}`,
        attachment: attachments,
        htmlContent: `
          <h2>Thank You for Your Order!</h2>
          <p>Hi ${orderDetails.userDetails.name || 'Valued Customer'},</p>
          <p>Thank you for choosing FluidPower Group. We're pleased to confirm that your order has been received and is being processed.</p>
          
          <h3>Order Details:</h3>
          <p><strong>Order Number:</strong> ${orderDetails.orderNumber || 'Unknown'}</p>
          ${priceBreakdownHTML}
          
          <h3>Shipping Details:</h3>
          <p>
            ${orderDetails.userDetails.address || 'N/A'}<br>
            ${orderDetails.userDetails.suburb || ''} ${orderDetails.userDetails.state || ''} ${orderDetails.userDetails.postcode || ''}
          </p>
          
          <p>We've attached a copy of your order confirmation for your records.</p>
          
          <p>If you have any questions about your order, please don't hesitate to contact us:</p>
          <ul>
            <li>Phone: 0409 517 333</li>
            <li>Email: info@fluidpowergroup.com.au</li>
          </ul>
          
          <p>We appreciate you doing business with us!</p>
          
          <p>Best regards,<br>
          FluidPower Group Team</p>
        `
      };

      const customerResponse = await fetch(BREVO_API_URL, {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'api-key': BREVO_API_KEY,
        },
        body: JSON.stringify(customerEmailPayload)
      });

      if (!customerResponse.ok) {
        const data = await customerResponse.json();
        throw new Error(`Customer Email Error: ${customerResponse.status} - ${JSON.stringify(data)}`);
      }
    }

    return { success: true };
  } catch (error) {
    console.error('Detailed error in email sending:', {
      message: error.message,
      stack: error.stack,
      name: error.name
    });
    return { 
      success: false, 
      error: error.message,
      details: error
    };
  }
};